.header-container {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    place-items: center;
    padding: 1.5rem;
    background: rgba(211, 222, 223, 0.932);
}

.links ul {
    display: flex;
    justify-content: center;
}

.links li {
    margin: 0 10px;
}

.logo {
    font-size: 2em;
    text-shadow: 0 0 5px white;
    color: blue;
}

.user {
    width: 170px;
    height: 170px;
    border-radius: 50%;
    color: blue;
    pointer-events: none;
}

.aparecer {
    position: absolute;
    top: 135px;
    right: 435px;
    font-size: 14px;
    color: white;
    visibility: hidden;
}

.aparecer:hover {
    cursor: pointer;
    text-decoration: underline;
}

.picture:hover .aparecer {
    visibility: visible;
}


@media only screen and (max-width: 500px){
    .header-container{
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .logo {
        text-align: center;
        font-size: 1.3em;
    }
}